<template>
    <div>
        <canvas :height="customHeight" id="power-chart"></canvas>
    </div>
</template>

<script>
import Chart from 'chart.js';
import chartData from './chartTemplates/powerChartTemplate'
import Service from './Service'

export default {
    name: 'PowerHistory',
    props: [
        'customHeight'
    ],
    data(){
        return {
            chartData: chartData,
            firstTime: true,
        }
    },
    methods: {
        createChart(chartId, chartData) {
            const ctx = document.getElementById(chartId);
            new Chart(ctx, {
            type: chartData.type,
            data: chartData.data,
            options: chartData.options,
            });
        },
        update(data){
            this.chartData.options.scales.yAxes[0].scaleLabel.labelString='Power (W)';
            this.chartData.options.scales.xAxes[0].scaleLabel.labelString='Time (hours)';
            this.chartData.data.datasets[0].label = 'Power (W)'
            this.chartData.data.datasets[0].data = data;

            var today = new Date();
            today.setSeconds(today.getSeconds() - data.length*10);
            this.chartData.data.labels = [];
            for (var i=0;i<data.length;i++){
                if (i == 0 || i == data.length-1 || i % 24 == 0)
                    this.chartData.data.labels.push(today.getHours() + ':' + today.getMinutes());
                else
                    this.chartData.data.labels.push('');
                today.setSeconds(today.getSeconds()+10);
            }
            this.createChart('power-chart', this.chartData);
            this.$emit('set_loading', false);
            this.chartData.options.animation = false; // do not animate next time
        },
        fetchHistory(){
            Service.getPowerHistory(this.update);
        }
    },
    mounted() {
        this.timer = setInterval(this.fetchHistory, 1000*10);
        this.fetchHistory();
    }
}
</script>
